.nav-top{
  background-color: #F8F8FA;
  padding: 20px 0;
  .red-line{
    position: relative;
    &::after{
      content: '';
      width: 100%;
      height: 3px;
      background-color: #D6D6E0;
      position: absolute;
      top: -20px;
      left: 0;
      transition: all 0.2s linear;

    }
    &:hover::after{
      background-color: #DE1E3A;
    }
  }
  a{
    color: #AEAEAF;
    font-size: 15px;
    line-height: 18px;
    margin-right: 100px;
    &:hover{
      color: black;
    }
  }
}

nav{
  padding: 34px 0;
  .nav-kirish{
    transition: all 0.5s linear;
    &:hover{
      transform: translateX(10px) rotate(360deg);
    }
  }
  a{
    font-size: 16px;
    line-height: 19px;
    color: #2F2F2F;
    &:hover{
      color: black;
    }
  }
  .courses{
    width: 124px;
    height: 44px;
    border: 1px solid #E6E6E6;
    filter: drop-shadow(0px 11px 14px rgba(0, 0, 0, 0.0339543));
    margin-left: 65px;
    transition: all 0.2s linear;
    &:hover{
      transform: scale(1.1);
      box-shadow: 0 0 4px #003846;
    }

  }
}

header{
  background-color: #003846;
  position: relative;
  padding-bottom: 110px;
  h1{
    font-size: 60px;
    line-height: 60px;
    margin-top: 130px;
  }
  p{
    font-size: 22px;
    line-height: 33px;
    margin-top: 18px;
  }
  img{
    width: 450px;
  }
  .header-cards{
    position: absolute;
    bottom: -225px;
    @media (max-width: 776px) {
      position: static;
    }


  }
  .header-card{
    box-sizing: border-box;
    background-color: #F8F8FA;
    padding: 40px;
    cursor: pointer;
    transition: all 0.2s linear;
    &:hover{
      background-color: #0A4958;
      box-shadow: 0 0 10px grey;
    }
    &:hover h5{
      color: #ffffff;
    }
    &:hover .for-arrow-right-card{
      display: block;
    }
    &:hover .icon-java{
      background-color: #ffffff;
    }
    h5{
      color: #636364;
      font-size: 23px;
      line-height: 30px;
      margin-top: 40px;
    }
    .for-arrow-right-card{
      width: 29px;
      height: 12px;
      display: none;
      margin-top: 18px;
    }
    .icon-java{
      width: 170px;
      height: 170px;

      background-color: black;
    }
  }
}

